import React from 'react';

import './index.scss';

type Props = {
  page: number;
  endPage: number;
  onClick: (page: number) => void;
};

function getPages(nowPage: number, endPage: number) {
  if (endPage <= 5) {
    const pages = Array.from({ length: endPage }, (_, index) => index + 1);
    return pages;
  }

  if (nowPage <= 3) {
    const pages = Array.from({ length: 5 }, (_, index) => index + 1);
    return pages;
  }

  if (nowPage >= endPage - 2) {
    const pages = Array.from({ length: 5 }, (_, index) => endPage - 4 + index);
    return pages;
  }

  const pages = Array.from({ length: 5 }, (_, index) => nowPage - 2 + index);
  return pages;
}

function Pagination({ page, endPage, onClick }: Props) {
  const pages: number[] = getPages(page, endPage);

  return (
    <div className='pagination'>
      <button
        className='pagination__button pagination__button--prev'
        type='button'
        disabled={endPage < 1 || endPage < page || page <= 1}
        onClick={() => {
          if (endPage < 1 || endPage < page || page <= 1) {
            return;
          }
          onClick(page - 1);
        }}
      >
        <i className='material-icons'>chevron_left</i>
      </button>

      {pages.map((pageNum) => {
        const isNowPage = pageNum === page;
        return (
          <button
            key={`pagination-${pageNum}`}
            className={`pagination__button pagination__button--page ${
              isNowPage ? 'pagination__button--selected' : ''
            }`}
            type='button'
            disabled={isNowPage}
            onClick={() => onClick(pageNum)}
          >
            {pageNum}
          </button>
        );
      })}

      <button
        className='pagination__button pagination__button--next'
        type='button'
        disabled={page >= endPage}
        onClick={() => {
          onClick(page + 1);
        }}
      >
        <i className='material-icons'>navigate_next</i>
      </button>
    </div>
  );
}

export default Pagination;
